import { Component, OnInit } from '@angular/core';
import { HttpService } from '../shared/http.service';
import { Router } from '@angular/router';
import { ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-sellerdetails',
  templateUrl: './sellerdetails.page.html',
  styleUrls: ['./sellerdetails.page.scss'],
})
export class SellerdetailsPage implements OnInit {




  constructor(private router: Router, private http: HttpService, route: ActivatedRoute) {
    route.params.subscribe(val => {
      this.getCategoryList()
      this.list()
      



    });
  }

  ngOnInit() {
  }

  categoryList: any = [];


  selectedFile: File = null;
  selectedaddressproof: File = null;
  listOfCat = [];

  list() {
    this.http.get('/read_all_category',).subscribe((response: any) => {
      this.listOfCat = response.records;
    }, (error: any) => {
      console.log(error);
    }
    );
  }

  navigateHome() {
    this.router.navigate(['/social-media-details'])
  }

  getCategoryList() {
    this.http.get('/read_category',).subscribe((response: any) => {
      console.log(response.records);
      this.categoryList = response.records
      console.log(response.records);
      console.log(this.categoryList);

    }, (error: any) => {
      console.log(error);
    }
    );
  }

  image: any;
  idproof: File = null;
  addressproof: File = null;
  idproofimage: any;
  addressproofimage: any;
  tbid: any;
  userdetails: any = JSON.parse(atob(localStorage.getItem("24hrs-user-data")));
  store_name = this.userdetails.storename;

  MediaFileSelected(event) {
    this.selectedFile = event.target.files[0] as File;
  }

  MediaFileSelected_addressproof(event1) {
    this.addressproof = event1.target.files[0] as File;
  }

  MediaFileSelected_idproof(event2) {
    this.idproof = event2.target.files[0] as File;
  }


  onClickSubmit(data) {
    this.image = this.selectedFile;
    const formdata = new FormData();
    //formdata.append("category", data.category);
    formdata.append("tbid", this.userdetails.id);
    formdata.append("store_name", this.store_name);
    formdata.append("address_line_1", data.address_line_1);
    formdata.append("address_line_2", data.address_line_2);
    formdata.append("city", data.city);
    formdata.append("state", data.state);
    formdata.append("pincode", data.pincode);
    formdata.append("seller_name", data.category);
    formdata.append("unit", data.username);
    formdata.append("store_logo", this.image);
    formdata.append("address_proof", this.addressproof);
    formdata.append("id_proof", this.idproof);


    this.http.postFormData("/seller_update_profile", formdata).subscribe((response: any) => {
      console.log(response);
      this.router.navigate(['/social-media-details'])


    }, (error: any) => {
      console.log(error);
    }
    );
  }

  // var body = "category = " + data.category + "subcategory = " + data.subcategory + "product_name = " + data.product_name + "cost = " + data.cost + "unit = " + data.unit + "product_image = " + this.image + "description = " + data.description;
  // console.log(body);

}

